import divider from '@/components/divider.vue';

//引入网络请求
import $H from '@/common/request.js';
import $C from '@/common/config.js';
import $U from '@/common/util.js';
export default {
	components:{
		divider
	},
	data() {
		return {
			background:{
				backgroundColor: '#fff',
			},
			showDetail:false,
			showIndex:-1,
			type:1,
			page:1,
			show:false,
			reload: true,
			loadMore: false,
			status: 'more',
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多'
			},
			listData:[],
			nodata:false
		}
	},
	
	
	onLoad() {
		this.getData()
	},
	
	onReachBottom() {
			this.loadMore = true;
			this.page++;  
			if (this.status == 'noMore') {
				return;
			}
			this.status = 'loading';
		    this.getData()
	},
	
	onPullDownRefresh() {
		this.reload=true;
		this.page=1;
		this.status = 'more';
		this.getData()
	},
	
	
	methods: {
	
	toMingXi(id){
		uni.navigateTo({
			url:'../yongJinMingXi/yongJinMingXi?id='+id+"&type=2"
		})
	},
		
		getData(){
			
			let data={
				type:2,
				size:10,
				current:this.page,
			}
			
			// 发送到服务端
			$H.get("/merchant_task/page/all",data).then((res) => {
				//请求成功
				console.log(res)
				let list = res.data.records
				
				let pages=res.data.pages;
				
				console.log(this.page)
				if(pages>1&&this.page<=pages){
					this.listData = this.reload ? list : this.listData.concat(list);
					this.nodata=false;
				}
				else if(pages==0){
					this.listData=[];
					this.nodata=true;
					this.status ='noMore'
					
				}
				else if(pages==1){
					this.listData = list
					this.status ='noMore'
					this.nodata=false;
				}
				else{
					this.status ='noMore'
				}
				
				this.reload = false;
				
			}).catch((e) => {
				
				//请求失败
				console.log("失败"+e)
			})
		},
		
		
		
		changeShow(ind){
			this.showIndex=ind;
			this.showDetail=!this.showDetail
		}
	}
}
